import React, { Component } from 'react'
import './set.css'
import { Modal,WingBlank, Flex } from 'antd-mobile';
import { Link} from 'react-router-dom'
 const alert=Modal.alert;
export default class Set extends Component {
    /**
     * 
     */
    exitLogin=()=>{
        if(JSON.parse(localStorage.getItem('key'))===null){
            alert('');
        }else{
             if(JSON.parse(localStorage.getItem('key')).iname === ''){
                alert('');
            }else{
                alert("!")
                localStorage.setItem('key',JSON.stringify({"iname":'',"realname":null}))
                //window.location.href="http://localhost:3000/#/login"
            } 
        }
       
    }
    change=()=>{
        if(JSON.parse(localStorage.getItem('key'))===null){
            alert('');
        }else{
            if(JSON.parse(localStorage.getItem('key')).iname===''){
                alert('');
            }else{
                window.location.href="http://localhost:3000/#/password"
            }
        }
    }
    render() {
        return (
            <div>
                <div className='set_nav'>
                    <Link to='/appTaber'><div style={{ fontSize: 26, color: 'white', lineHeight: '50px', marginLeft: '30%' }} className='iconfont icon-changyongtubiao-xianxingdaochu-zhuanqu--copy'></div></Link>
                    <h2 className='set_nav_h2'></h2>
                </div>
                <WingBlank>
                    <div style={{ width: '100%', height: '50px', marginTop: '5%', borderTop: '1px solid #f1f1f1', borderBottom: '1px solid #f1f1f1' }}>
                        <Flex>
                            <div className="set_div"></div>
                            <p style={{ fontSize: 20, lineHeight: '50px', color: '#5a5a5a', marginLeft: '4%' }}></p>
                            <div className='set_div1'></div>
                        </Flex>
                    </div>
                </WingBlank>
                <WingBlank>
                   <div style={{ width: '100%', height: '50px', marginTop: '5%', borderTop: '1px solid #f1f1f1', borderBottom: '1px solid #f1f1f1' }}>
                        <Flex>
                            <div className="set_div2"></div>
                            <p onClick={()=>this.change()} style={{ fontSize: 20, lineHeight: '50px', color: '#5a5a5a', marginLeft: '4%' }}></p>
                            <div className='set_div1'></div>
                        </Flex>
                    </div>
                </WingBlank>
                <WingBlank>
                    <div style={{ width: '100%', height: '50px', marginTop: '5%', borderTop: '1px solid #f1f1f1', borderBottom: '1px solid #f1f1f1' }}>
                        <Flex>
                            <div className="set_div3"></div>
                            <p style={{ fontSize: 20, lineHeight: '50px', color: '#5a5a5a', marginLeft: '4%' }}></p>
                            <div className='set_div1'></div>
                        </Flex>
                    </div>
                </WingBlank>
                <WingBlank>
                    <div style={{ width: '100%', height: '50px', marginTop: '5%', borderTop: '1px solid #f1f1f1', borderBottom: '1px solid #f1f1f1' }}>
                        <Flex>
                            <div className="set_div4"></div>
                            <p style={{ fontSize: 20, lineHeight: '50px', color: '#5a5a5a', marginLeft: '4%' }}></p>
                            <div className='set_div1'></div>
                        </Flex>
                    </div>
                </WingBlank>
                <WingBlank>
                    <div style={{ width: '100%', height: '50px', marginTop: '5%', borderTop: '1px solid #f1f1f1', borderBottom: '1px solid #f1f1f1' }}>
                        <Flex>
                            <div className="set_div5"></div>
                            <p style={{ fontSize: 20, lineHeight: '50px', color: '#5a5a5a', marginLeft: '4%' }}></p>
                            <div className='set_div1'></div>
                        </Flex>
                    </div>
                </WingBlank>
                <div>
                    <WingBlank>
                        <div className="exit_login">
                            <button onClick={()=>this.exitLogin()}></button>
                        </div>
                    </WingBlank>
                </div>
            </div>
        )
    }
}
